.add_item_button {
    cursor: pointer;
    background-color: var(--white);
    border: 1px solid var(--darkerWhite);
    border-radius: 5px;
    font-size: 16px;
    padding: 10px;
    width: 80%;
}

.add_item_button:hover {
    background-color: var(--red);
    color: var(--white);
}

.checked_button {
    background-color: var(--liteBlack);
    border: 1px solid var(--darkerWhite);
    color: var(--white);
    cursor: pointer;
    height: 35px;
    width: 25%;
}

.checked_button:hover {
    text-decoration: underline;
}